<template>
    <section class="cloud-list">
        <div class="container-fluid">
            <div class="row">
                <div class="col-10">
                    <div class="tab-content">
                        <slot v-for="(list, key, index) in words">
                        <cloud v-if="index==0"
                            :data="list"
                            class="tab-pane fade show active"
                            font="Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif"
                            :id="'zip-'+key"
                            :fontSizeMapper="fontSizeMapper"/>
                        <cloud v-else
                            :data="list"
                            class="tab-pane fade"
                            font="Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif"
                            :id="'zip-'+key"
                            :fontSizeMapper="fontSizeMapper"/>
                        </slot>
                    </div>
                </div>
                <div class="col">
                    <div class="list-group" id="zipcodes">
                        <slot v-for="(list, zipcode, index) in words">
                        <a v-if="index==0"
                           class="list-group-item list-group-item-action active"
                           data-toggle="list"
                           :href="'#zip-'+zipcode">{{zipcode}}</a>
                        <a v-else
                           class="list-group-item list-group-item-action"
                           data-toggle="list"
                           :href="'#zip-'+zipcode">{{zipcode}}</a>
                        </slot>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import Cloud from 'vue-d3-cloud'
import axios from 'axios'
export default {
    name: 'CloudsList',
    created () {
        axios.get('localhost:5000/words').then(resp=>{ this.words = resp.body })
    },
    data() {return {
        words: {
            // 12324: [
            //     {text: 'adhusbef', value: 213},
            //     {text: '231', value: 26},
            //     {text: 'adfgayuehusbef', value: 13},
            //     {text: '678', value: 9723},
            // ],
            // 324234:[
            //     {text: '3456', value: 3},
            //     {text: 'ik', value: 246},
            //     {text: '98uhv', value: 613},
            //     {text: 'qax', value: 973},
            // ]
        },
        fontSizeMapper: word => Math.log2(word.value) * 5
    }},
    compute: {

    },
    components: {
        Cloud
    }
}
</script>
<style lang="stylus">
.wordCloud
    font-family Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif
</style>

